<template>
  <el-card id="myTag">
    <p>
      <span style="color:#67C23A" class="el-icon-collection-tag"></span>
    </p>
    <hr/>

    <div v-for="tag in tags">
      <el-tag type="success" closable @close="deleteTag(tag.id)" style="margin-top: 10px" @click="editTag(tag.id)">
        <el-link type="success" :underline="false">{{tag.name}}</el-link>
      </el-tag>
    </div>
    <div style="margin-top: 8%">
      <el-button class="el-icon-plus" size="mini" @click="addTag"></el-button>
    </div>

  </el-card>
</template>

<script>
import tag from '@/api/tag';

export default {
  name: 'myTag',
  data() {
    return {
      tags: []
    };
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      tag.getTag().then(resp => {
        if (resp.sta === '00') {
          this.tags = resp.data;
        }
      });
    },
    deleteTag(tagId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        tag.deleteTag(tagId).then(resp => {
          if (resp.sta === '00') {
            this.load();
            this.$message.success('');
          } else {
            this.$message.error(resp.message || '');
          }
        });
      }).catch(() => {});
    },
    editTag(tagId) {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(({value}) => {
        if (value == null || value.length <= 0) {
          this.$message.warning('');
          return;
        }
        tag.updateTag(tagId, value).then(resp => {
          if (resp.sta === '00') {
            this.load();
            this.$message.success('');
          } else {
            this.$message.error(resp.message || '');
          }
        });
      }).catch(() => {
      });
    },
    addTag() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(({value}) => {
        if (value == null || value.length <= 0) {
          this.$message.warning('');
          return;
        }
        tag.addTag(value).then(resp => {
          if (resp.sta === '00') {
            this.load();
            this.$message.success('');
          } else {
            this.$message.error(resp.message || '');
          }
        });
      }).catch(() => {
      });
    }
  }
};
</script>
<style scoped>
  #myTag {
    /*-moz-box-shadow: 0px 6px 0px #333333;*/
    /*-webkit-box-shadow: 0px 6px 0px #333333;*/
    /*box-shadow: 0px 3px 10px #333333;*/
    margin: 30px 10px;

  }
</style>



