<template>
  <el-card id="forgetPwd">
    <div style="margin: auto;width: 25%">
      <el-form ref="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="userName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="newPassword" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="confirmPassword" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="mail" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">

          <el-input v-model="mailCode" placeholder=""></el-input>

        </el-form-item>
      </el-form>
    </div>
    <div>
      <el-button type="text" @click="sendMail">
        &nbsp;&nbsp;
        <i class="el-icon-coffee-cup" v-if="!sendMailFlag"/>
        <i class="el-icon-loading" v-if="sendMailFlag"/>
      </el-button>
      <el-button type="text" @click="forgetPwd">&nbsp;&nbsp;<i class="el-icon-potato-strips"></i></el-button>
    </div>
  </el-card>
</template>
<script>
import user from '@/api/user';

export default {
  name: 'forgetPwd',
  data() {
    return {
      userName: '',
      mail: '',
      mailCode: '',
      newPassword: '',
      confirmPassword: '',
      sendMailFlag: false
    };
  },
  methods: {
    sendMail() { // 
      var reg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
      if (this.userName.length <= 0) {
        this.$message.warning('');
        return;
      }
      if (!reg.test(this.mail)) { // 
        this.$message.warning('');
        return;
      }
      this.sendMailFlag = true;
      user.sendMail(this.mail).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
        } else {
          this.$message.error(resp.message || '');
        }
        this.sendMailFlag = false;
      }).catch(() => {
        this.sendMailFlag = false;
      });
    },
    forgetPwd() {
      if (this.userName.length <= 0) {
        this.$message.warning('');
        return;
      }
      var reg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
      if (!reg.test(this.mail)) { // 
        this.$message.warning('');
        return;
      }
      if (this.newPassword.length < 6) {
        this.$message.warning('6');
        return;
      }
      if (this.newPassword !== this.confirmPassword) {
        this.$message.warning('');
        return;
      }
      if (this.mailCode.length <= 0) {
        this.$message.warning('');
        return;
      }
      user.forgetPassword(this.userName, this.mailCode, this.newPassword).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
        } else {
          this.$message.success(resp.message || '');
        }

        scrollTo(0, 0);
        this.$router.push({ // 
          path: '/'
        });
      });
    }
  }
};
</script>
<style scoped>
  #forgetPwd {
    margin: 20px 5% 6% 5%;
  }

</style>



