<template>
  <el-card id="account">

    <div id="updatePwd">
      <div>
        <h3></h3>
        <el-form ref="form" label-width="80px">
          <el-form-item label="">
            <el-input v-model="oldPassword" placeholder="" show-password></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="newPassword" placeholder="" show-password></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="confirmPassword" placeholder="" show-password></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-button type="text" style="width: 100%;color: black">{{mail}}</el-button>
            <el-button type="text" style="width: 85%;" @click="updatePwdSendMail()">
              
              <i class="el-icon-loading" v-if="updatePwdSendFlag"/>
            </el-button>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="updatePwdMailCode" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div style="margin-left: 45%">
        <el-button icon="el-icon-potato-strips" @click="updatePwd()"></el-button>
      </div>
    </div>

    <el-divider/>

    <div id="updateMail">
      <div>
        <h3></h3>
        <el-form ref="form" label-width="100px">
          <el-form-item label="">
            <el-button type="text" style="width: 100%;color: black">{{mail}}</el-button>
            <el-button type="text" style="width: 85%;" @click="updateMailSendMailToOld()">
              
              <i class="el-icon-loading" v-if="updateMailToOldSendFlag"/>
            </el-button>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="oldMailCode" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="newMail" placeholder=""></el-input>
            <el-button type="text" style="width: 85%;" @click="updateMailSendMailToNew()">
              
              <i class="el-icon-loading" v-if="updateMailToNewSendFlag"/>
            </el-button>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="newMailCode" placeholder=""></el-input>

          </el-form-item>
        </el-form>
      </div>
      <div style="margin-left: 45%">
        <el-button icon="el-icon-cherry" @click="updateMail()"></el-button>
      </div>
    </div>

    <el-divider/>

    <div id="updateReward">
      <div>
        <h3></h3>
        <el-form ref="form" label-width="100px">
          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              :action="upload"
              :headers="getToken()"
              :on-success="handleAvatarSuccess"
              :show-file-list="false"
              :before-upload="beforeAvatarUpload">
              <img v-if="userReward" :src="userReward" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

        </el-form>
      </div>
      <div style="margin-left: 45%">
        <el-button icon="el-icon-orange" @click="updateReward()"></el-button>
      </div>
    </div>

  </el-card>
</template>

<script>
import user from '@/api/user';
import {global} from "@/config/global"

export default {
  name: 'account',
  data() {
    return {
      oldPassword: '',
      newPassword: '',
      confirmPassword: '',
      updatePwdMailCode: '', // 
      mail: '', // 
      oldMailCode: '', // 
      newMail: '',    // 
      newMailCode: '',  // 
      updatePwdSendFlag: false,
      updateMailToOldSendFlag: false,
      updateMailToNewSendFlag: false,
      userReward: '',
      upload: global.apiBaseUrl + '/blogs/uploadImg'
    };
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      user.getUserMail().then(resp => {
        if (resp.sta === '00') {
          this.mail = resp.data;
        }
      });
      user.getUserReward().then(resp => {
        if (resp.sta === '00') {
          this.userReward = global.apiBaseUrl + resp.data;
        } else {
          this.userReward = '';
        }
      });
    },
    getToken() {
      return {'Authorization': this.$store.state.token};
    },
    updateReward() {
      if (this.userReward === '') { return; }

      user.updateReward(this.userReward.replace(global.apiBaseUrl, '')).then(resp => {
        if (resp.sta === '00') {
          this.$message.success(resp.message);
          this.load();
        } else {
          this.$message.error(resp.message || '');
        }
      });
    },
    handleAvatarSuccess(res, file) {
      this.userReward = global.apiBaseUrl + res.data;
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 1;
      if (!isJPG && !isPNG) {
        this.$message.error(' jpg/png ');
      }
      if (!isLt2M) {
        this.$message.error(' 1MB!');
      }
      return true;
    },
    updatePwdSendMail() {  // 
      this.updatePwdSendFlag = true;
      this.sendMail(this.mail);
    },
    updateMailSendMailToOld() {  // 
      this.updateMailToOldSendFlag = true;
      this.sendMail(this.mail);
    },
    updateMailSendMailToNew() {  // 
      this.updateMailToNewSendFlag = true;
      this.sendMail(this.newMail);
    },
    sendMail(mail) {
      var reg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
      if (!reg.test(mail)) { // 
        this.$message.warning('');
        this.updatePwdSendFlag = false;
        this.updateMailToOldSendFlag = false;
        this.updateMailToNewSendFlag = false;
        return;
      }
      user.sendMail(mail).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
          this.updatePwdSendFlag = false;
          this.updateMailToOldSendFlag = false;
          this.updateMailToNewSendFlag = false;
        } else {
          this.updatePwdSendFlag = false;
          this.$message.error(resp.message || '');
        }
      }).catch(() => {
        this.updatePwdSendFlag = false;
        this.updateMailToOldSendFlag = false;
        this.updateMailToNewSendFlag = false;
      });
    },
    updatePwd() {
      if (this.oldPassword.length <= 0) {
        this.$message.warning('');
        return;
      }

      if (this.newPassword.length < 6) {
        this.$message.warning('6');
        return;
      }

      if (this.newPassword !== this.confirmPassword) {
        this.$message.warning('');
        return;
      }

      if (this.updatePwdMailCode.length <= 0) {
        this.$message.warning('');
        return;
      }

      user.updatePassword(this.oldPassword, this.newPassword, this.updatePwdMailCode).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
          this.$store.commit('logout');// token
          this.$router.push({ // 
            path: '/'
          });
        } else {
          this.$message.error(resp.message || '');
        }
      });
    },
    updateMail() {
      var reg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
      if (!reg.test(this.newMail)) { // 
        this.$message.warning('');
        return;
      }

      if (this.oldMailCode.length <= 0) { //
        this.$message.warning('');
        return;
      }

      if (this.newMailCode.length <= 0) { //
        this.$message.warning('');
        return;
      }

      user.updateMail(this.newMail, this.oldMailCode, this.newMailCode).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
          this.newMail = '';
          this.oldMailCode = '';
          this.newMailCode = '';
          this.load();
        } else {
          this.$message.error(resp.message || '');
        }
      });
    }
  }
};
</script>
<style scoped>
  #account {
    margin: 1% 5%;
    text-align: left;
  }

  #updatePwd {
    padding-left: 30%;
    width: 30%;
  }

  #updateMail {
    padding-left: 30%;
    width: 30%;
  }

  #updateReward {
    padding-left: 30%;
    width: 30%;
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #000000;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #000000;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 176px;
    height: 176px;
    text-align: center;
  }

  .avatar {
    width: 176px;
    height: 176px;
    display: block;
  }

</style>



