import axios from 'axios';
import {Message, MessageBox} from 'element-ui';
import store from '@/store/store';
import router from '@/router/router';
import {global} from "../config/global";

// axios
var prod;
const service = axios.create({
  baseURL: global.apiBaseUrl, // apibase_url
  timeout: 15000, // ,
  header: 'Content-Type:application/x-www-form-urlencoded'
});

// request
service.interceptors.request.use(config => {
  // alert(store.state.token) //token
  if (store.state.token !== '') {
    config.headers['Authorization'] = store.state.token; // token 
  }
  return config;
}, error => {
  // Do something with request error
  console.log(error); // for debug
  Promise.reject(error);
});

// respone
service.interceptors.response.use(
  response => {
    /**
     * code200 
     * 200200
     */
    const res = response.data;

    if ((typeof (res.code) !== 'undefined' && res.code !== 200) || (typeof (res.status) !== 'undefined' && res.status !== true)) {
      // 
      if (typeof (res.code) !== 'undefined' && res.code === 205) {
        return response.data;
      }

      if (typeof (res.code) !== 'undefined' && res.code !== 403) {
        // 
        // Message({
        //   message: res.code + ' : ' + res.message,
        //   type: 'error',
        //   duration: 5 * 1000
        // })

        // 
        Message({
          message: res.message,
          type: 'error',
          duration: 5 * 1000
        });
      }

      if (typeof (res.status) !== 'undefined' && res.status !== true) {
        Message({
          message: '',
          type: 'error',
          duration: 5 * 1000
        });
      }
      // 403:Token  (/) ;
      if (res.code === 403) {
        store.commit('logout');
        MessageBox.confirm('', '', {
          confirmButtonText: ' ',
          cancelButtonText: ' ',
          type: 'warning'
        }).then(() => {
          // window.location.href = '#/'
          router.push({ // 
            path: '/blog'
          });
          location.reload();
        }).catch(() => {
          router.push({ // 
            path: '/blog'
          });
          location.reload();
        });
      }
      return Promise.reject('error');
    } else {
      return response.data;
    }
  },
  error => {
    console.log(':' + error.message);// for debug
    var message;
    if (error.response.status === 504) {
      message = '';
    } else {
      message = error.message;
    }
    Message({
      message: message,
      type: 'error',
      duration: 5 * 1000
    });
    return Promise.reject(error);
  }
);

export default service;



