<template>
  <div>
    <div style="text-align: right;margin-bottom: 5px">
      <!--<el-button style="width: 7%;text-align: center" v-if="searchFlag == true" @click="returnNormal()"></el-button>-->

      <el-input placeholder="" v-model="searchName" suffix-icon="el-icon-search"
                style="width: 30%;" clearable @keyup.enter.native="searchSubmit" @clear="searchClear"/>
    </div>
    <div v-loading="loading">
      <el-table :data="blogData" style="width: 100%" :border="true">
        <el-table-column label="" width="400">
          <template slot-scope="scope">
            <i class="el-icon-tickets"></i>
            <span style="margin-left: 10px">{{ scope.row.title }}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ getTime(scope.row.time) }}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="250">
          <template slot-scope="scope">
            <i class="el-icon-user"></i>
            <span style="margin-left: 10px">{{ scope.row.user.name }}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <i class="el-icon-wind-power"></i>
            <span style="margin-left: 10px" v-if="scope.row.state == 1"></span>
            <span style="margin-left: 10px;color:#F56C6C " v-if="scope.row.state == 0"></span>
          </template>
        </el-table-column>

        <el-table-column label="">

          <template slot-scope="scope">
            <el-button size="mini" v-if="scope.row.state == 1" type="warning" plain
                       @click="deleteBlog(scope.row.id)">
              
            </el-button>
            <el-tooltip placement="top" effect="light" v-if="scope.row.state == 0">
              <div slot="content"><br/></div>
              <el-link :underline="false" style="margin-right: 68%;color: #E6A23C"><i class="el-icon-question"></i>
              </el-link>
            </el-tooltip>

          </template>
        </el-table-column>
      </el-table>
    </div>
    <br/>
    <div style="padding-bottom: 4%">
      <el-pagination
        :page-size="pageSize"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="currentChange"
        :current-page="currentPage"
        @prev-click="currentPage=currentPage-1"
        @next-click="currentPage=currentPage+1"
        :hide-on-single-page="true">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import blog from '@/api/blog';
import date from '@/utils/date';

export default {
  name: 'blogManage',
  data() {
    return {
      blogData: [],
      total: 0,        // 
      pageSize: 10,    // 
      currentPage: 1,   // 
      loading: true,
      searchName: '',
      searchFlag: false   //  true  false
    };
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      if (this.searchFlag) {
        blog.adminSearchBlog(this.searchName, this.currentPage, this.pageSize).then(resp => {
          if (resp.sta === '00') {
            this.blogData = resp.data.rows;
            this.total = resp.data.total;
          }

          this.loading = false;
        });
      } else {
        blog.adminGetBlog(this.currentPage, this.pageSize).then(resp => {
          if (resp.sta === '00') {
            this.blogData = resp.data.rows;
            this.total = resp.data.total;
          }

          this.loading = false;
        });
      }
    },
    currentChange(currentPage) { // 
      this.currentPage = currentPage;
      this.load();
      scrollTo(0, 0);
    },
    searchSubmit() {
      this.currentPage = 1;
      this.loading = true;
      this.searchFlag = true;
      this.load();
    },
    searchClear() {
      this.currentPage = 1;
      this.loading = true;
      this.searchFlag = false;
      this.load();
    },
    // returnNormal() {
    //   this.currentPage = 1;
    //   this.searchName = '';
    //   this.loading = true;
    //   this.searchFlag = false;
    //   this.load();
    // },
    getTime(time) {
      return date.timeago(time);
    },
    deleteBlog(id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        blog.adminDeleteBlog(id).then(resp => {
          if (resp.sta === '00') {
            this.$message.success('');
            this.load();
          } else {
            this.$message.error(resp.message || '');
          }
        });
      }).catch(() => {
      });
    }
  }
};
</script>



