<template>
  <el-card id="bar">
    <div>
      <el-row>
        <el-menu :default-active="activeIndex" class="el-menu-demo" :router="true" mode="horizontal"
                 @select="handleSelect">

          <el-menu-item index="1" route="/blog"></el-menu-item>
          <el-menu-item index="2" style="margin-left: 2%" route="/message"></el-menu-item>
          <el-menu-item index="3" style="margin-left: 2%" route="/announcement"></el-menu-item>

          <el-menu-item id="space" index="" class="hidden-xs-only"/>
          <el-menu-item/>

          <div style="width: 20%;float: left;margin: 10px 2% 0px -5%" class="hidden-xs-only">
            <el-input placeholder="" v-model="searchTxt" suffix-icon="el-icon-search"
                      clearable @keyup.enter.native="searchSubmit" @clear="searchClear"/>
          </div>

          <el-menu-item class="hidden-xs-only" v-if="this.$store.state.token === ''" index=""
                        @click="loginFormVisible = true">
            <el-button type="text"></el-button>
          </el-menu-item>

          <el-menu-item class="hidden-xs-only" v-if="this.$store.state.token === ''" index=""
                        @click="registerFormVisible = true">
            <el-button type="text"></el-button>
          </el-menu-item>

          <el-dialog title="" :visible.sync="loginFormVisible" width="21%">
            <el-form :model="form">
              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.loginName" placeholder="" prefixIcon="el-icon-user-solid"/>
              </el-form-item>
              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.loginPwd" placeholder="" :show-password="form.loginShowPwd"
                          @keyup.enter.native="userLogin" prefixIcon="el-icon-lock"/>
              </el-form-item>
              <el-button @click="userLogin">&nbsp;&nbsp;<i class="el-icon-check"></i></el-button>
              <router-link to="/forgetPwd">
                <el-button @click="loginFormVisible = false">&nbsp;&nbsp;<i class="el-icon-right"></i></el-button>
              </router-link>
            </el-form>
          </el-dialog>

          <el-dialog title="" :visible.sync="registerFormVisible" width="30%">

            <el-form :model="form" style="width: 450px;margin: 0 auto;">
              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.registerName" placeholder="" prefixIcon="el-icon-user-solid"/>
              </el-form-item>
              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.registerPwd" placeholder="" :show-password="form.loginShowPwd"
                          prefixIcon="el-icon-lock"/>
              </el-form-item>

              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.registerConfirmPwd" placeholder="" :show-password="form.loginShowPwd"
                          prefixIcon="el-icon-bell"/>
              </el-form-item>

              <!--<el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.registerInviteCode" placeholder="" prefixIcon="el-icon-s-promotion"/>
              </el-form-item>-->
              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.registerMail" placeholder="" prefixIcon="el-icon-message"/>
              </el-form-item>
              <el-form-item :label-width="formLabelWidth">
                <el-input v-model="form.registerMailCode" placeholder="" prefixIcon="el-icon-key"/>
              </el-form-item>
              <el-button @click="sendMail">
                &nbsp;&nbsp;
                <i class="el-icon-coffee-cup" v-if="!sendMailFlag"/>
                <i class="el-icon-loading" v-if="sendMailFlag"/>
              </el-button>
              <el-button @click="userRegister">&nbsp;&nbsp;<i class="el-icon-check"></i></el-button>
            </el-form>
          </el-dialog>

          <el-submenu class="hidden-xs-only" index="4" v-if="this.$store.state.token!==''" :router="true">
            <template slot="title">[&nbsp;&nbsp;{{this.$store.state.name}}&nbsp;&nbsp;]</template>

            <el-menu-item route="/newBlog" index="4-1">&nbsp;&nbsp;&nbsp;
              <i class="el-icon-edit"></i>
              
            </el-menu-item>
            <el-menu-item route="/myBlog" index="4-3">&nbsp;&nbsp;&nbsp;
              <i class="el-icon-s-home"></i>
              
            </el-menu-item>
            <el-menu-item route="/account" index="4-2">&nbsp;&nbsp;&nbsp;
              <i class="el-icon-s-tools"></i>
              
            </el-menu-item>

            <el-menu-item route="/admins/userManage" index="4-4" v-if="this.$store.state.roles.indexOf('ADMIN') > -1">
              &nbsp;&nbsp;&nbsp;
              <i class="el-icon-loading"></i>
            </el-menu-item>

            <el-menu-item @click="logout">&nbsp;&nbsp;&nbsp;
              <i class="el-icon-switch-button"/>
            </el-menu-item>
          </el-submenu>

        </el-menu>
      </el-row>

    </div>

  </el-card>
</template>
<script>
import user from '@/api/user';
import 'element-ui/lib/theme-chalk/display.css';

export default {
  name: 'bar',
  data() {
    return {
      searchTxt: '', // 
      activeIndex: '1', // ->
      loginFormVisible: false, // 
      registerFormVisible: false, // 
      form: { // 
        loginName: '',
        loginPwd: '',
        loginShowPwd: true, // 
        registerName: '', // 
        registerPwd: '',
        registerConfirmPwd: '',
        registerMail: '',
        registerMailCode: '',
        // registerInviteCode: ''
      },
      formLabelWidth: '0px',
      sendMailFlag: false
    };
  },
  watch: {
    // path
    '$route.path': {
      deep: true,
      immediate: true,
      handler(to, from) {
        if (to === '/') {
          this.activeIndex = '1';
        } else if (to === '/message') {
          this.activeIndex = '2';
        } else if (to === '/announcement') {
          this.activeIndex = '3';
        } else {
          this.activeIndex = '4';
        }
      }
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      //        console.log(key, keyPath);
      if (key != null && key !== '') { this.activeIndex = key; }
    },
    // egg() {
    //   this.$notify({
    //     title: '',
    //     message: ' ',
    //     type: 'success',
    //     duration: 120
    //   });
    // },
    userLogin() { // 
      if (this.form.loginName.length <= 0 || this.form.loginPwd.length <= 0) {
        this.$message.info('');
        return;
      }
      user.login(this.form.loginName, this.form.loginPwd).then(resp => {
        if (resp.sta === '00') {
          this.form.loginName = '';
          this.form.loginPwd = '';
          this.$store.commit('login', resp.data);// token
          this.$message.success(resp.message);
        } else {
          this.$message.error(resp.message);
        }

        this.loginFormVisible = false;
      });
    },
    logout() {  // 
      user.logout().then(resp => {
        this.$store.commit('logout');// token
        this.$message.success(resp.message);
        this.$router.push({ // 
          path: '/blog'
        });
      });
    },
    sendMail() { // 
      var reg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
      if (!reg.test(this.form.registerMail)) { // 
        this.$message.warning('');
        return;
      }
      this.sendMailFlag = true;
      user.sendMail(this.form.registerMail).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
        } else {
          this.$message.success(resp.message || '');
        }

        this.sendMailFlag = false;
      }).catch(() => {
        this.sendMailFlag = false;
      });
    },
    userRegister() { // 
      // 
      if (this.form.registerName.length <= 0 || this.form.registerPwd.length <= 0 || this.form.registerConfirmPwd.length <= 0 ||
          this.form.registerMail.length <= 0 || this.form.registerMailCode.length <= 0) {
        this.$message.warning('');
        return;
      }
      if (this.form.registerPwd !== this.form.registerConfirmPwd) {
        this.$message.warning('');
        return;
      }
      if (this.form.registerPwd.length < 6) {
        this.$message.warning('6');
        return;
      }
      var reg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);

      if (!reg.test(this.form.registerMail)) { // 
        this.$message.warning('');
        return;
      }

      user.register(this.form.registerName, this.form.registerPwd, this.form.registerMail, this.form.registerMailCode).then(resp => {
        if (resp.sta === '00') {
          this.$message.success('');
          this.registerFormVisible = false;
        } else {
          this.$message.error(resp.message || '');
        }

      });
    },
    searchSubmit() {
      if (this.searchTxt.length <= 0) { return; }
      this.$router.push({ // 
        path: '/searchBlog/' + this.searchTxt
      });
      // this.searchTxt = '';// 
    },
    searchClear() {
      this.$router.push({ // 
        path: '/'
      });
    }
  }
};
</script>
<style>
  #bar {
    margin-top: -50px;
    margin-bottom: 0px;
  }

  #space {
    margin: 0 21% 0 20%;
  }
</style>



